
.app {
  padding: 30px;
  background-color: darkgray;
  width: 550px;
  margin: 0 auto;
}

.todo {
  align-items: center;
  display: flex;
  font-size: 18px;
  justify-content: space-between;
}

.todo-form{
  display: flex;
}


.input-form-grup{
  width: 100%;
  padding: 0 15px 0 0;
}

.card-heading{
  margin-top: 25px;
  background: rgb(2,0,36);
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);
  padding: 15px;
  color: #fff;
  text-align: center;
  font-size: 20px;
}

.heading-main{
  font-weight: bold;
}

.app h1 i{
  font-size: 50px;
  color: #fff;
}

.personal-info{
  position: absolute;
  bottom: 5%;
  right: 5%;
}

.personal-info a{
  text-decoration: none;
  color: #000;
  font-family: cursive;
  font-weight: bold;
}
.personal-info a:hover{
  color: crimson;
  transition: .3s;
}





@media (max-width: 575px) {
  .app{
    width: auto;
    padding: 30px 0;
  }
  .card-heading{
    font-size: 17px;
  }
  .card-body{
    padding: 5px!important;
  }
}